h2 {
    color: green;
    font-style: italic;
}

.success {
    color: green;
    font-size: 20px;
    background-color: lightgray;
    padding: 10px;
    border: solid;
    border-width: 3px;
    border-radius: 5px;
    border-color: green;
    margin-bottom: 15px;
}

.error {
    color: red;
    font-size: 20px;
    background-color: lightgray;
    padding: 10px;
    border: solid;
    border-width: 3px;
    border-radius: 5px;
    border-color: red;
    margin-bottom: 15px;
}